/// Colour variables used in the original build
/// @group colors
/// @type color
// Colours are named by colour value and then mapped to purpose value.

$colors: (
    'primary': #009ede,
    'primary-light': #4cc2f1,
    'primary-dark': #006e9b,
    'secondary': #70a400,
    'secondary-light': #bad80a,
    'secondary-dark': #598300,
    'tertiary': #ef7013,
    'tertiary-light': #ffb900,
    'tertiary-dark': #d1610e,
    'quaternary': #dd3817,
    'quaternary-dark': #b02c12,
    'navy': #062b52,
    'white': #fff,
    'alabaster': #f8f8f8,
    'cloud': #e5e5e5,
    'grey': #b2b2b2,
    'boulder': #767676,
    'dark-grey': #333f48,
    'black': #000
);

$color-white: map-get($colors, 'white');
$color-alabaster: map-get($colors, 'alabaster');
$color-cloud: map-get($colors, 'cloud');
$color-grey: map-get($colors, 'grey');
$color-boulder: map-get($colors, 'boulder');
$color-dark-grey: map-get($colors, 'dark-grey');
$color-black: map-get($colors, 'black');

$color-navy: map-get($colors, 'navy');

$color-primary: map-get($colors, 'primary');
$color-secondary: map-get($colors, 'secondary');
$color-tertiary: map-get($colors, 'tertiary');
$color-quaternary: map-get($colors, 'quaternary');

$color-primary-light: map-get($colors, 'primary-light');
$color-secondary-light: map-get($colors, 'secondary-light');
$color-tertiary-light: map-get($colors, 'tertiary-light');

$color-primary-dark: map-get($colors, 'primary-dark');
$color-secondary-dark: map-get($colors, 'secondary-dark');
$color-tertiary-dark: map-get($colors, 'tertiary-dark');
$color-quaternary-dark: map-get($colors, 'quaternary-dark');

$color-link: $color-primary-dark;
$color-link-hover: $color-primary;
$color-link-visited: $color-primary-dark;
